export default {
	data() {
		return {
			emailNum: '',
			
			opcity: 0.5,
			nosendCode: false,
			sendbtn: {
				text: '获取验证码',
				codeTime: 60
			},
			checkCode:''
		
		}
	},
	onLoad() {

	},
	onReady() {

	},
	methods: {
		change(e) {
			if (e.detail.value.length >= 3) {
				this.opcity = 1
			} else {
				this.opcity = 0.5
			}
		},
		//发送邮箱注册验证码
		sendCode(e) {
			this.nosendCode = true
			uni.request({
				url: this.baseUrl + "/email-register-code",
				data: {
					Email: this.emailNum
				},
				method: "POST",
				success: (res) => {
					console.log(res)
					if (res.data.status == 1) {
						this.sendMsgCodeTimer()
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
					} else {
						this.nosendCode = false
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
					}
				}
			})
		},
		// 发送短信验证码计时器
		sendMsgCodeTimer() {
			this.timerId = setInterval(() => {
				let codeTime = this.sendbtn.codeTime;
				codeTime--;
				this.sendbtn.codeTime = codeTime;
				this.sendbtn.text = codeTime + "S";
				if (codeTime < 1) {
					clearInterval(this.timerId);
					this.sendbtn.text = "重新获取";
					this.nosendCode = false
					this.sendbtn.codeTime = 60;
				}
			}, 1000);
		},
		//跳转到下一步
		next(){
			if(this.checkCode!=''&&this.opcity==1){
				uni.navigateTo({
					url:"./setPassword?checkCode="+this.checkCode+"&emailNum="+this.emailNum
				})
			}else{
				uni.showToast({
					title:"请输入验证码",
					icon:"none"
				})
			}
		}
	}
}
